.white {
  color: #ffffff;
}

.btn-container {
  position: absolute;
  right: 5%;
  transform: translateX(-5%);
}

.overlay-container {
  background: #f6f6f7;
  width: 350px;
  min-height: 300px;
  height: auto;
  flex-direction: column;
}

.image-container {
  height: 150px;
  width: 150px;
  overflow: hidden;
  margin: 25px auto;
  border-radius: 50%;
}

.image-container img {
  height: 100%;
  margin-left: -15px;
}

.name-container {
  width: 100%;
  background: linear-gradient(100deg, #ff00b4, #b5e7ff, #73ffff);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;

  h1 {
    font-size: 18px;
    color: #ffff;
    letter-spacing: 1.5px;
  }
}

.info-container {
  text-align: center;
  padding: 30px;

  .vocation {
    font-size: 15px;
    color: #5ab6e2;
  }
  .email,
  .number,
  .website {
    font-size: 12px;
  }

  .quote {
    font-size: 15px;
  }

  .website {
    color: #5ab6e2;
  }

  .viewfull-container {
    border: 1px solid #5ab6e2;
    border-radius: 30%;
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
}

@keyframes overlay-anim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.overlay-anim {
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-name: overlay-anim;
  opacity: 0;
  width: 100%;
}
